import React from "react";
import { Card } from "../ui/card";
import { Avatar, AvatarFallback, AvatarImage } from "../ui/avatar";

const AllArticlePage = () => {
  return (
    <div className="grid gap-8 sm:grid-cols-2 lg:grid-cols-3">
      <Card className="group relative overflow-hidden translate-all hover:shadow-lg">
        <div className="p-6">
          <div className="relative mb-4 h-47 w-full overflow-hidden rounded-xl">
            <img
              src={
                "https://www.shutterstock.com/image-photo/calm-weather-on-sea-ocean-600nw-2212935531.jpg"
              }
              alt="some stuff"
              className="object-cover"
            />
          </div>
          {/* Article Content */}
          <h3 className="text-xl font-semibold">Title</h3>
          <div className="mt-6 flex items-center justify-between">
            <div className="flex items-center gap-3">
              <Avatar>
                <AvatarImage src="" />
                <AvatarFallback>CN</AvatarFallback>
              </Avatar>
              <span className="text-sm"> Sworna D. Tuladhar</span>
            </div>
            <div className="text-sm">21 Feb</div>
          </div>
        </div>
      </Card>
    </div>
  );
};

export default AllArticlePage;
